.LogoSearch {
  display: flex;
  gap: 0.57rem;
		
}
.Search
{
	display: flex;
	background-color: var(--inputColor);
	border-radius: 10px;
	padding: 5px;
}
.Search>input{
	background-color: transparent;
	border: none;
	outline: none;
}
.s-icon
{
	display: flex;
	justify-content: center;
	align-items: center;
	background: linear-gradient(106.23deg, #f99827, #f95f35 100%);
	border-radius: 5px;
	padding: 4px;
	color: white;
	cursor: pointer;
}
