.ProfileCard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 1.5rem;
  gap: 1rem;
		/* overflow-x: clip; */
		background: var(--cardColor);
		width: 100%;
		overflow: hidden;
}
.ProfileImages {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
		position: relative;
		width: 100%;
}
.ProfileImages > img:nth-of-type(1) {
  width: 100%;
  height: 170px;
  object-fit: cover;
}
.ProfileImages > img:nth-of-type(2) {
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  position: absolute;
  bottom: -3rem;
  object-fit: cover;
  box-shadow: var(--profileShadow);
}

.ProfileName {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
		gap: 10px;
}

.ProfileName>span:nth-of-type(1)
{
	font-weight: bold;
}

/* follow status */
.followStatus{
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap:0.75rem
}
.followStatus >hr{
	width: 85%;
	border: 1px solid var(--hrColor);
}
.followStatus>div{
	display: flex;
	align-items: center;
	justify-content: space-around;
	gap: 1rem;
	width: 80%;
}
.follow
{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap:.4rem
}
.follow>span:nth-of-type(1)
{
	font-weight: bold;
}
.follow>span:nth-of-type(2)
{
	color: var(--gray);
font-size: 13px;
}

.vl{
	height: 150%;
	border: 1px solid var(--hrColor);
}

.ProfileCard>span{
	align-self: center;
	font-weight: bold;
	color: orange;
	margin-bottom: 1rem;
	cursor: pointer;
}