.Post
{
	display: flex;
	flex-direction: column;
	gap: 1rem;
	border-radius: 1rem;
	padding: 1rem;
	background-color: var(--cardColor);
}




.Post>img
{
	width: 100%;
	max-height: 20rem;
	object-fit: cover;
	border-radius: .5rem;
}
.PostReact
{
	display: flex;
	justify-content: flex-start;
	gap: 1.5rem;
}

.commentCount{
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 3px;
}